<template>
  <div class="header-box">
    <div class="head-logo">锦州港敞顶箱电子管理平台</div>
    <div class="head-handle">
      <p><span>上次登录时间：{{loginTime}}</span><span>上次登录IP：{{loginIp}}</span></p>
      <a href="javascript:void(0)" @click="cancelLation" >退出</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Headerbox',
  data () {
    return {
      loginIp:'',
      loginTime:''
    }
  },mounted(){
  	
  	var _this = this;
  	_this.loginTime=sessionStorage.getItem("loginTime");
  	_this.loginIp=sessionStorage.getItem("loginIp");
  },
  methods:{
  	
  	linkTo: function(url) {
          this.$router.push(url)
    },
  	cancelLation:function() {
  		var _this = this;
  		this.$ajax.get('/userCenter/loginOut')
      	.then(function (response) {
      		
      			sessionStorage.clear('objStr');
				_this.linkTo('/login');
      		
      	
      	})
      	.catch(function (error) {
        	console.log(error);
      	});
  		
  	}
 }
}
</script>

<style scoped>
.header-box{
  height: 60px;
  background: #2f8ff2;
  /* background-image: -webkit-linear-gradient(left,#3dcde9 60%, #1dd19b); */
  background-image: url('./../../assets/images/header_img2.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.header-box .head-logo{
  float: left;
  width: 250px;
  height: 60px;
  line-height: 60px;
  padding: 0 0 0 20px;
  font-size: 15px;
  color: #fff;
}
.header-box .head-handle{
  height: 60px;
  padding: 0 20px;
  margin: 0 0 0 250px;
  text-align: right;
  overflow: hidden;
}
.header-box .head-handle>a{
  display: inline-block;
  margin: 10px 0 0;
  color: #fff;
  vertical-align: middle;
}
a.exitsystem{
  width: auto;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
}
a.exitsystem:hover{
  background: rgba(242, 47, 47, 0.7);
}
.header-box .head-handle>p{
  display: inline-block;
  margin: 7px 80px 0 0;
  padding: 5px;
  border: 1px #999 dashed;
  text-align: left;
  border-radius: 4px;
  font-size: 0.5rem;
  color: #aaa;
  vertical-align: middle;
}
.header-box .head-handle>p span{
  display: block;
}
</style>



