<template>
  <div class="home-box">
    <Header></Header>
    <div class="main-box" v-bind:style="{height: mainHeight+'px'}">
      <LeftMenu></LeftMenu>
      <div class="maincon-box" v-bind:style="{height: mainHeight+'px'}">
        <bread></bread>
        <div class="maincon-scroll" v-bind:style="{height: scrollHeight+'px'}">
          <mainCon></mainCon>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Header from './common/Header.vue'
import LeftMenu from './common/LeftMenu.vue'
import bread from './common/bread.vue'
import mainCon from './common/mainCon.vue'

export default {
  name: 'Homebox',
  components: {
    Header,
    LeftMenu,
    bread,
    mainCon
  },
  data () {
    return {
      mainHeight: 0,
      scrollHeight: 0
    }
  },
  methods: {
    // 计算高度
    getMainHeight: function() {
      var winH = $(window).height();
      this.mainHeight = winH - 60;
      this.scrollHeight = winH - 60 - 35;
    }
  },
  created: function() {
    this.getMainHeight();
  }
}
</script>

<style scoped>
.home-box{
  height: 100%;
  overflow: hidden;
}
.main-box{
  height: 100%;
}
.maincon-box{
  margin: 0 0 0 170px;
}
.maincon-scroll{
  height: auto;
  padding: 15px;
  overflow: hidden;
  overflow-y: auto;
}
</style>



