<template>
  <div class="leftmun-box" v-bind:style="{height: leftmunHeight+'px'}">
    <ul>
      <li v-for="(item, index) in items" :key="index">
        <div class="mun-stair" @click="clickMenufirst(index)"><span :class="item.icon"></span>{{item.name}}</div>
        <div class="mun-sub-list" v-if="item.sub && item.sub.length">
          <p v-for="(sub, key) in item.sub" :key="key">{{sub.name}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Leftmun',
  data () {
    return {
      leftmunHeight: 0,
      items: [
        {
          name: '基础信息管理',
          icon: 'icon-caiwu',
          url: '',
          sub: [],
          isChech: false
        },
        {
          name: '运营管理',
          icon: 'icon-yunying',
          url: '',
          sub: [],
          isChech: false
        },
        {
          name: '管理人员',
          icon: 'icon-guanlirenyuan',
          url: '',
          sub: [],
          isChech: false
        },
        {
          name: '系统管理员',
          icon: 'icon-xitongglry',
          url: '',
          sub: [],
          isChech: false
        },
        {
          name: '开发人员',
          icon: 'icon-vcard',
          url: '',
          sub: [],
          isChech: false
        }
      ]
    }
  },
  methods: {
    // 计算高度
    getleftmunHeight: function() {
      var winH = $(window).height();
      this.leftmunHeight = winH - 60;
    },
    clickMenufirst: function(e) {
      //
    }
  },
  created: function() {
    this.getleftmunHeight();
  }
}
</script>

<style scoped>
.leftmun-box{
  float: left;
  width: 170px;
  background: #fff;
  border-right: 1px #dbdbdb solid;
}
.leftmun-box li{
  min-height: 24px;
  line-height: 24px;
}
.leftmun-box li:hover{
  background: #f9f9f9;
}
.leftmun-box li.check{
  margin: 0 -1px 0 0;
  border-right: 1px #2f8ff2 solid;
}
.mun-stair{
  height: 24px;
  padding: 8px 0;
  box-sizing: initial;
  cursor: pointer;
}
.mun-stair:hover{
  color: #2f8ff2;
}
.mun-stair>span{
  display: inline-block;
  width: 21px;
  height: 21px;
  line-height: 21px;
  margin: 0 15px 0 20px;
  vertical-align: middle;
  border-radius: 4px;
  text-align: center;
  font-size: 16px;
}
.mun-stair:hover span{
  color: #2f8ff2;
}
.mun-sub-list{
  padding: 0 10px 10px 55px;
  overflow: hidden;
}
.mun-sub-list>p{
  /* border-bottom: 1px #eee solid; */
  font-size: 12px;
  cursor: pointer;
}
</style>



